.container-4 {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  margin: 2rem; }

.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (max-width: 800px) {
    .contact-container {
      margin-top: 10rem; } }
  .contact-container__header1 {
    font-size: 4rem;
    font-weight: 400; }
  .contact-container__header2 {
    font-size: 2rem;
    font-weight: 200;
    font-style: italic; }
  .contact-container__icons {
    margin-top: 2rem; }
  .contact-container__icon {
    width: 5rem;
    height: 5rem; }
