.container-3 {
  min-height: 100rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  margin: 2rem; }

.portfolio-container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .portfolio-container__heading {
    font-size: 4rem;
    font-weight: 400;
    margin: 0 auto; }
  .portfolio-container__buttons {
    display: flex; }
    .portfolio-container__buttons a {
      text-decoration: none; }
    @media (max-width: 800px) {
      .portfolio-container__buttons {
        justify-content: center; } }
  .portfolio-container__heading2 {
    font-size: 2rem;
    font-weight: 300;
    font-style: italic; }
  .portfolio-container__box {
    width: 100vw;
    height: 60vh;
    margin-top: 5rem;
    display: flex;
    align-content: center;
    align-items: center;
    flex-direction: column; }
  .portfolio-container__heading3 {
    font-size: 3rem;
    font-weight: 500;
    margin-top: 2.5rem;
    margin-bottom: 2rem;
    text-align: center; }
  .portfolio-container__img {
    width: 40rem;
    height: 25rem;
    margin-top: 2rem;
    margin-right: 6rem;
    margin-left: 2rem;
    box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.6); }
  .portfolio-container__content {
    display: flex;
    align-items: center;
    align-self: center;
    width: 80rem;
    font-size: 2.75rem;
    font-weight: 100; }
    @media (max-width: 800px) {
      .portfolio-container__content {
        flex-direction: column;
        width: 50rem; } }
  .portfolio-container__description {
    font-size: 1rem; }
    @media (max-width: 800px) {
      .portfolio-container__description {
        width: 70vw;
        margin-top: 1.5rem; } }

.btn {
  color: #fff;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  text-transform: uppercase;
  font-family: inherit;
  font-weight: 500;
  font-size: 1.1rem;
  letter-spacing: 1.5px;
  cursor: pointer;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  outline: none;
  border-width: 1px;
  border-style: initial;
  border-color: transparent;
  border-image: initial;
  border-radius: 10rem;
  padding: 1.5rem 4rem;
  margin-top: 2rem;
  margin-right: 2rem;
  transition: all 0.2s ease-out 0s; }
