.container {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5rem; }

.textarea {
  width: 25rem;
  height: 2.5rem;
  margin-bottom: 1rem;
  resize: none;
  padding: 4px 1px; }
  .textarea__big {
    height: 7rem; }
  .textarea::placeholder {
    font-size: 1rem; }
  .textarea:focus {
    outline: none; }

.select {
  background-color: transparent;
  border-radius: 0;
  appearance: none;
  width: 25rem;
  height: 2.5rem;
  margin-bottom: 1rem; }
