.container {
  min-height: 100vh;
  padding-top: 5rem;
  display: flex; }
  .container a {
    text-decoration: none; }

.main-container {
  position: relative;
  left: 10rem; }
  @media (max-width: 800px) {
    .main-container {
      position: relative;
      left: 30%; } }
  .main-container__heading1 {
    font-size: 4.5rem;
    font-weight: 100; }
  .main-container__heading2 {
    font-size: 2.5rem;
    font-weight: 100; }

.btn {
  color: #fff;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  text-transform: uppercase;
  font-family: inherit;
  font-weight: 500;
  font-size: 1.1rem;
  letter-spacing: 1.5px;
  cursor: pointer;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  outline: none;
  border-width: 1px;
  border-style: initial;
  border-color: transparent;
  border-image: initial;
  border-radius: 10rem;
  padding: 1.5rem 4rem;
  margin-top: 2rem;
  margin-right: 2rem;
  transition: all 0.2s ease-out 0s; }

:root {
  --color-primary-light: #ff3366;
  --color-primary-dark: #ba265d; }

* {
  margin: 0;
  padding: 0; }

*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: var(--color-grey-dark-2);
  background-size: cover;
  background-repeat: no-repeat; }
